
:root {
    font-variant-ligatures: none;
}

html {
    font-size: 16px;
}

body {
    font-family: droid-sans, 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: var(--fw-400);
    font-size: 1rem;
    line-height: 1.556;
    font-style: normal;
    color: black;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


h1, .h1, h2, .h2, h3, .h3, h4, .h4, .h99 {
    font-family: droid-sans, sans-serif;
    font-style: normal;
    margin-bottom: 0.5rem;
    font-weight: var(--fw-700);
    color: var(--col-blue-400);
}

h1, .h1 {
    font-size: 40px;
}
h2, .h2 {
    font-size: 32px;
}
h3, .h3 {
    font-size: 24px;
}

.fs-900 {
    font-size: 40px;
}
.fs-700 {
    font-size: 32px;
}

.fs-7 {
    font-size: 0.889rem;
}

strong, b {
    font-weight: var(--fw-700);
}

.border-bottom-thick {
    border-bottom: 8px solid #00beff;
    margin-bottom: 1em;
    padding-bottom: 1em;
}
.border-bottom-thick-white {
    border-bottom: 8px solid #fff;
    margin-bottom: 1em;
    padding-bottom: 1em;
}